import React, { useEffect, useRef, useState } from 'react';
import { database } from '../firebase/config';
import { ref, set, push, onValue, off } from 'firebase/database';
import '../styles/Canvas.css';

const Canvas = ({ roomId, isDrawer, gameState, brushColor, brushSize }) => {
  const canvasRef = useRef(null);
  const contextRef = useRef(null);
  const [isDrawing, setIsDrawing] = useState(false);
  const [prevPoint, setPrevPoint] = useState(null);
  
  // Use a different pattern: separate drawing board (local) vs history (remote)
  // Store the drawing history to prevent duplicate processing
  const drawingHistoryRef = useRef([]);
  
  useEffect(() => {
    console.log("Canvas mounted/updated");
    console.log("Game state:", gameState);
    console.log("Is drawer:", isDrawer);
    
    if (!canvasRef.current) {
      console.error("Canvas ref is null");
      return;
    }

    const canvas = canvasRef.current;
    canvas.width = 800;
    canvas.height = 600;
    
    const context = canvas.getContext('2d');
    context.lineCap = 'round';
    context.lineJoin = 'round';
    context.strokeStyle = brushColor;
    context.lineWidth = brushSize;
    contextRef.current = context;
    
    // Initialize canvas with white background
    context.fillStyle = '#ffffff';
    context.fillRect(0, 0, canvas.width, canvas.height);
    
    // Listen for drawing updates from Firebase - now using a list of drawing commands
    const drawingHistoryRef = ref(database, `rooms/${roomId}/drawingHistory`);
    onValue(drawingHistoryRef, (snapshot) => {
      const historyData = snapshot.val();
      if (!historyData) return;
      
      // Completely redraw canvas from the history
      redrawCanvasFromHistory(historyData);
    });
    
    return () => {
      console.log("Canvas unmounting");
      off(drawingHistoryRef);
    };
  }, [roomId, gameState, brushColor, brushSize, isDrawer]);

  // Completely redraw the canvas from history
  const redrawCanvasFromHistory = (history) => {
    if (!contextRef.current || !canvasRef.current) return;
    
    const ctx = contextRef.current;
    const canvas = canvasRef.current;
    
    // Start with a clean canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.fillStyle = '#ffffff';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    
    // Apply each drawing action in order
    Object.values(history || {}).forEach(action => {
      if (action.type === 'draw') {
        drawLineOnCanvas(
          ctx,
          action.fromX,
          action.fromY,
          action.toX,
          action.toY,
          action.color,
          action.size
        );
      }
    });
  };
  
  // Just draw a line on the canvas context - no state updates
  const drawLineOnCanvas = (ctx, fromX, fromY, toX, toY, color, size) => {
    ctx.save();
    ctx.strokeStyle = color;
    ctx.lineWidth = size;
    ctx.beginPath();
    ctx.moveTo(fromX, fromY);
    ctx.lineTo(toX, toY);
    ctx.stroke();
    ctx.restore();
  };

  // Save a draw action to Firebase
  const saveDrawAction = (fromX, fromY, toX, toY, color, size) => {
    if (!roomId) return;
    
    const drawingHistoryRef = ref(database, `rooms/${roomId}/drawingHistory`);
    const newActionRef = push(drawingHistoryRef);
    
    const action = {
      type: 'draw',
      fromX,
      fromY,
      toX,
      toY,
      color,
      size,
      timestamp: Date.now()
    };
    
    set(newActionRef, action)
      .catch(error => {
        console.error("Error saving drawing action:", error);
      });
  };

  // Clear the drawing history
  const clearDrawingHistory = () => {
    if (!roomId) return;
    
    const drawingHistoryRef = ref(database, `rooms/${roomId}/drawingHistory`);
    set(drawingHistoryRef, null)
      .catch(error => {
        console.error("Error clearing drawing history:", error);
      });
  };

  const startDrawing = (e) => {
    e.preventDefault();
    console.log("Start drawing called");
    console.log("Is drawer:", isDrawer, "Game state:", gameState);
    
    if (!isDrawer || gameState !== 'drawing') {
      console.log("Cannot draw - not drawer or wrong game state");
      return;
    }
    
    const canvas = canvasRef.current;
    const rect = canvas.getBoundingClientRect();
    let x, y;
    
    // Check if it's a mouse or touch event
    if (e.type === 'mousedown') {
      x = e.clientX - rect.left;
      y = e.clientY - rect.top;
    } else if (e.type === 'touchstart') {
      x = e.touches[0].clientX - rect.left;
      y = e.touches[0].clientY - rect.top;
    }
    
    console.log("Drawing started at:", x, y);
    
    setPrevPoint({ x, y });
    setIsDrawing(true);
  };

  const draw = (e) => {
    e.preventDefault();
    
    if (!isDrawing || !isDrawer || gameState !== 'drawing') {
      return;
    }
    
    const canvas = canvasRef.current;
    const rect = canvas.getBoundingClientRect();
    let x, y;
    
    // Check if it's a mouse or touch event
    if (e.type === 'mousemove') {
      x = e.clientX - rect.left;
      y = e.clientY - rect.top;
    } else if (e.type === 'touchmove') {
      x = e.touches[0].clientX - rect.left;
      y = e.touches[0].clientY - rect.top;
    }
    
    // Draw directly on canvas for immediate feedback
    if (contextRef.current) {
      drawLineOnCanvas(
        contextRef.current,
        prevPoint.x,
        prevPoint.y,
        x,
        y,
        brushColor,
        brushSize
      );
    }
    
    // Save to Firebase history
    saveDrawAction(
      prevPoint.x,
      prevPoint.y,
      x,
      y,
      brushColor,
      brushSize
    );
    
    setPrevPoint({ x, y });
  };

  const stopDrawing = (e) => {
    e.preventDefault();
    
    if (!isDrawing) return;
    
    console.log("Drawing stopped");
    setIsDrawing(false);
    setPrevPoint(null);
  };

  const clearCanvas = () => {
    if (!isDrawer || gameState !== 'drawing') {
      console.log("Cannot clear canvas - not drawer or wrong game state");
      return;
    }
    
    if (!contextRef.current || !canvasRef.current) {
      console.error("Canvas or context is null");
      return;
    }
    
    console.log("Clearing canvas");
    
    // Clear canvas visually
    contextRef.current.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
    contextRef.current.fillStyle = '#ffffff';
    contextRef.current.fillRect(0, 0, canvasRef.current.width, canvasRef.current.height);
    
    // Clear drawing history in Firebase
    clearDrawingHistory();
  };

  return (
    <div className="canvas-wrapper">
      <canvas
        className="drawing-canvas"
        ref={canvasRef}
        onMouseDown={startDrawing}
        onMouseMove={draw}
        onMouseUp={stopDrawing}
        onMouseLeave={stopDrawing}
        onTouchStart={startDrawing}
        onTouchMove={draw}
        onTouchEnd={stopDrawing}
      ></canvas>
      
      {isDrawer && gameState === 'drawing' && (
        <button className="clear-button" onClick={clearCanvas}>
          Clear Canvas
        </button>
      )}
    </div>
  );
};

export default Canvas;