import React, { useState, useEffect, useRef } from 'react';
import { database } from '../firebase/config';
import { ref, push, onValue, off } from 'firebase/database';
import '../styles/ChatBox.css';

const ChatBox = ({ roomId, currentUser, currentWord, gameState, currentDrawer, handleGuess }) => {
  const [messages, setMessages] = useState([]);
  const [message, setMessage] = useState('');
  const messagesEndRef = useRef(null);
  const messagesContainerRef = useRef(null);
  const prevMessageLengthRef = useRef(0); // To track if messages were actually added

  useEffect(() => {
    // Reference to the messages in Firebase
    const messagesRef = ref(database, `rooms/${roomId}/messages`);
    
    // Listen for new messages
    onValue(messagesRef, (snapshot) => {
      const messagesData = snapshot.val();
      if (messagesData) {
        const messagesList = Object.entries(messagesData).map(([id, msg]) => ({
          id,
          ...msg,
          timestamp: new Date(msg.timestamp)
        }));
        
        // Sort by timestamp
        messagesList.sort((a, b) => a.timestamp - b.timestamp);
        
        setMessages(messagesList);
      } else {
        setMessages([]);
      }
    });

    return () => {
      off(messagesRef);
    };
  }, [roomId]);

  useEffect(() => {
    // Only scroll to bottom when new messages are added (not on initial load)
    if (messages.length > prevMessageLengthRef.current) {
      scrollToBottom();
      prevMessageLengthRef.current = messages.length;
    }
  }, [messages]);

  const scrollToBottom = () => {
    if (messagesEndRef.current && messagesContainerRef.current) {
      // Use scrollIntoView on the messages container's child rather than page scroll
      messagesContainerRef.current.scrollTop = messagesContainerRef.current.scrollHeight;
    }
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
  };

  const handleMessageSubmit = (e) => {
    e.preventDefault();
    
    if (!message.trim()) return;
    
    // Check if the game is in drawing state and the user is not the drawer
    const isGuessTime = gameState === 'drawing' && currentUser.uid !== currentDrawer;
    
    // If it's guess time, check if it's a correct guess
    if (isGuessTime && message.trim().toLowerCase() === currentWord.toLowerCase()) {
      console.log("This message is a correct guess!");
      
      // Process the guess through the parent component
      const guessProcessed = handleGuess(currentUser.uid, currentUser.displayName, message.trim());
      
      // If the guess was accepted (not a duplicate), clear the input
      if (guessProcessed) {
        setMessage('');
      }
      return;
    }
    
    // Regular message handling
    const messagesRef = ref(database, `rooms/${roomId}/messages`);
    const newMessage = {
      text: message,
      userId: currentUser.uid,
      username: currentUser.displayName || 'Anonymous',
      timestamp: Date.now(),
      type: 'regular'
    };
    
    push(messagesRef, newMessage)
      .then(() => {
        // Clear input after sending
        setMessage('');
      })
      .catch(error => {
        console.error("Error sending message:", error);
      });
  };

  return (
    <div className="chatbox">
      <div className="chatbox-header">
        <h3>Chat</h3>
      </div>
      
      <div className="messages-container" ref={messagesContainerRef}>
        {messages.map((msg) => (
          <div 
            key={msg.id} 
            className={`message ${msg.type} ${msg.userId === currentUser?.uid ? 'own' : ''}`}
          >
            <span className="username">{msg.username}</span>
            <span className="message-text">{msg.text}</span>
            <span className="timestamp">
              {msg.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
            </span>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>
      
      <form className="message-form" onSubmit={handleMessageSubmit}>
        <input
          type="text"
          placeholder={
            gameState === 'drawing' && currentUser?.uid !== currentDrawer
              ? "Type your guess here..."
              : "Type a message..."
          }
          value={message}
          onChange={handleMessageChange}
          disabled={gameState !== 'drawing' && gameState !== 'waiting' && gameState !== 'roundEnd'}
        />
        <button type="submit">Send</button>
      </form>
    </div>
  );
};

export default ChatBox;