import React, { useState, useEffect, useRef } from 'react';
import '../styles/Timer.css';

const Timer = ({ seconds, gameState }) => {
  const [displaySeconds, setDisplaySeconds] = useState(seconds);
  const prevGameStateRef = useRef(null);
  const timerRef = useRef(null);
  const initialSecondsRef = useRef(seconds);
  const roundEndStartTimeRef = useRef(null);
  
  // Handle game state changes
  useEffect(() => {
    // Reset visual countdown when game state changes
    if (prevGameStateRef.current !== gameState) {
      console.log(`Timer: Game state changed from ${prevGameStateRef.current} to ${gameState}`);
      
      // Clear any existing timer
      if (timerRef.current) {
        clearInterval(timerRef.current);
        timerRef.current = null;
      }
      
      // Special handling for roundEnd state
      if (gameState === 'roundEnd') {
        console.log(`Timer: Round end detected, starting with ${seconds} seconds`);
        initialSecondsRef.current = seconds;
        roundEndStartTimeRef.current = Date.now();
        setDisplaySeconds(seconds);
        
        // Start countdown timer for visual purposes
        timerRef.current = setInterval(() => {
          const elapsedSeconds = Math.floor((Date.now() - roundEndStartTimeRef.current) / 1000);
          const remainingSeconds = Math.max(0, initialSecondsRef.current - elapsedSeconds);
          
          console.log(`Timer: Round end countdown: ${remainingSeconds}`);
          setDisplaySeconds(remainingSeconds);
          
          if (remainingSeconds <= 0) {
            clearInterval(timerRef.current);
            timerRef.current = null;
          }
        }, 1000);
      } else {
        // For other states, just use the seconds from props
        setDisplaySeconds(seconds);
      }
      
      prevGameStateRef.current = gameState;
    } else if (gameState !== 'roundEnd') {
      // Update display seconds from props when not in roundEnd state
      setDisplaySeconds(seconds);
    }
    
    // Clean up on unmount
    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
    };
  }, [seconds, gameState]);
  
  // Calculate minutes and seconds
  const minutes = Math.floor(displaySeconds / 60);
  const remainingSeconds = displaySeconds % 60;
  
  // Format the time
  const formattedTime = `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
  
  // Determine timer color based on remaining time
  const getTimerColor = () => {
    if (displaySeconds <= 10) {
      return 'timer-critical';
    } else if (displaySeconds <= 30) {
      return 'timer-warning';
    }
    return 'timer-normal';
  };

  return (
    <div className={`timer ${getTimerColor()}`}>
      <div className="timer-display">{formattedTime}</div>
    </div>
  );
};

export default Timer;