import React, { useState, useEffect, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { database } from '../firebase/config';
import { ref, onValue, off, update, get, set, push } from 'firebase/database';
import { useAuth } from '../contexts/AuthContext';
import { useGame } from '../contexts/GameContext';
import Canvas from '../components/Canvas';
import ChatBox from '../components/ChatBox';
import ColorPicker from '../components/ColorPicker';
import PlayerList from '../components/PlayerList';
import Timer from '../components/Timer';
import GameControls from '../components/GameControls';
import Lobby from '../components/Lobby';
import { getRandomWord } from '../utils/wordGenerator';
import '../styles/Game.css';

const Game = () => {
  const { roomId } = useParams();
  const navigate = useNavigate();
  const { currentUser } = useAuth();
  const { setCurrentGame } = useGame();
  
  const [room, setRoom] = useState(null);
  const [players, setPlayers] = useState({});
  const [gameState, setGameState] = useState('waiting'); // waiting, choosing, drawing, roundEnd, gameEnd
  const [currentDrawer, setCurrentDrawer] = useState(null);
  const [currentWord, setCurrentWord] = useState('');
  const [hints, setHints] = useState([]);
  const [timer, setTimer] = useState(0);
  const [selectedColor, setSelectedColor] = useState('#000000');
  const [brushSize, setBrushSize] = useState(5);
  const [isHost, setIsHost] = useState(false);
  const [processingAutoStart, setProcessingAutoStart] = useState(false);
  const [guessedPlayers, setGuessedPlayers] = useState({});
  
  const timerRef = useRef(null);
  const roundsRef = useRef(1); // Initialize to 1, not 0
  const maxRoundsRef = useRef(3);
  const prevGameStateRef = useRef('waiting');
  const endRoundWordRef = useRef(''); // To store word for end-of-round announcement

  useEffect(() => {
    if (!currentUser) {
      console.log("No current user, redirecting to home");
      navigate('/');
      return;
    }

    console.log("Game component mounted, roomId:", roomId);
    console.log("Current user:", currentUser);

    // Reference to the room in Firebase
    const roomRef = ref(database, `rooms/${roomId}`);
    
    // Listen for room changes
    onValue(roomRef, (snapshot) => {
      const roomData = snapshot.val();
      console.log("Room data updated:", roomData);
      
      if (!roomData) {
        console.log("Room doesn't exist, redirecting to home");
        navigate('/');
        return;
      }
      
      setRoom(roomData);
      setPlayers(roomData.players || {});
      setGuessedPlayers(roomData.guessedPlayers || {});
      
      // Sync the local roundsRef with the server value
      if (roomData.roundNumber) {
        roundsRef.current = roomData.roundNumber;
        console.log("Synced round number from server:", roundsRef.current);
      }
      
      const newGameState = roomData.status || 'waiting';
      
      // Check if the game state is changing
      if (prevGameStateRef.current !== newGameState) {
        console.log(`Game state changing from ${prevGameStateRef.current} to ${newGameState}`);
        console.log("Current drawer when state changes:", roomData.currentDrawer);
        
        // If transitioning to choosing state but drawer is not set, auto-fix it
        if (newGameState === 'choosing' && !roomData.currentDrawer && !processingAutoStart) {
          console.log("Auto-fixing: Setting drawer since game state is 'choosing' but drawer is undefined");
          autoSetDrawer(roomData);
        }
        
        // Clear canvas when moving to choosing state (new round)
        if (newGameState === 'choosing' && prevGameStateRef.current !== 'choosing') {
          console.log("New round starting, clearing canvas");
          clearDrawingHistory();
        }
        
        // Reset guessed players when state changes to a new drawing round
        if (newGameState === 'drawing' && prevGameStateRef.current !== 'drawing') {
          console.log("New drawing round, resetting guessed players");
          const roomRef = ref(database, `rooms/${roomId}`);
          update(roomRef, { guessedPlayers: {} });
        }
        
        // If moving to roundEnd state, store the current word for announcement
        if (newGameState === 'roundEnd' && prevGameStateRef.current === 'drawing') {
          console.log("Round ending, storing word for announcement:", roomData.currentWord);
          endRoundWordRef.current = roomData.currentWord;
        }
        
        prevGameStateRef.current = newGameState;
      }
      
      // Normal state updates
      setGameState(newGameState);
      setCurrentDrawer(roomData.currentDrawer || null);
      setCurrentWord(roomData.currentWord || '');
      setHints(roomData.hints || []);
      setTimer(roomData.timer || 0);
      
      // Set isHost status
      const isUserHost = roomData.host === currentUser.uid;
      console.log("Is user host?", isUserHost);
      setIsHost(isUserHost);
      
      // Update context with game info
      setCurrentGame({
        roomId,
        roomData
      });

      // Handle game state logic
      if (roomData.status === 'roundEnd') {
        clearInterval(timerRef.current);
      }
      
      console.log("Game state updated:", roomData.status);
      console.log("Current drawer:", roomData.currentDrawer);
    });

    // Cleanup function
    return () => {
      console.log("Game component unmounting");
      clearInterval(timerRef.current);
      off(roomRef);
    };
  }, [roomId, currentUser, navigate, setCurrentGame, processingAutoStart]);

  // Clear drawing history in Firebase
  const clearDrawingHistory = () => {
    if (!roomId) return;
    
    console.log("Clearing drawing history");
    const drawingHistoryRef = ref(database, `rooms/${roomId}/drawingHistory`);
    set(drawingHistoryRef, null)
      .catch(error => {
        console.error("Error clearing drawing history:", error);
      });
  };

  // Auto-fix function to set drawer when game state is choosing but drawer is undefined
  const autoSetDrawer = (roomData) => {
    setProcessingAutoStart(true);
    
    try {
      console.log("Auto-fixing: Starting to set drawer");
      
      // Get player IDs in a sorted, consistent order
      const playerIds = Object.keys(roomData.players || {}).sort();
      if (playerIds.length < 2) {
        console.log("Auto-fixing: Not enough players to set drawer");
        setProcessingAutoStart(false);
        return;
      }
      
      // Get the round number
      const roundNumber = roomData.roundNumber || 1;
      
      // Select drawer based on round number for proper rotation
      // Use (roundNumber - 1) for zero-based indexing
      const nextDrawerIndex = (roundNumber - 1) % playerIds.length;
      const nextDrawer = playerIds[nextDrawerIndex];
      console.log(`Auto-fixing: Round ${roundNumber}, selecting drawer index ${nextDrawerIndex} (${nextDrawer})`);
      
      // Generate word options if none exist
      let wordOptions = roomData.wordOptions;
      if (!wordOptions || !Array.isArray(wordOptions) || wordOptions.length < 3) {
        wordOptions = [
          getRandomWord(),
          getRandomWord(),
          getRandomWord()
        ];
        console.log("Auto-fixing: Generated new word options:", wordOptions);
      }
      
      // Create a complete update object preserving all existing room data
      const updateData = {
        ...roomData,
        currentDrawer: nextDrawer,
        wordOptions: wordOptions,
        timer: roomData.timer || 30,
        guessedPlayers: {} // Reset guessed players for the new round
      };
      
      // Update using set to ensure all data is preserved
      const roomRef = ref(database, `rooms/${roomId}`);
      set(roomRef, updateData)
        .then(() => {
          console.log("Auto-fixing: Drawer set successfully to", nextDrawer);
          return get(roomRef);
        })
        .then((snapshot) => {
          const data = snapshot.val();
          console.log("Auto-fixing: Verification - Updated room data:", data);
          console.log("Auto-fixing: Verification - Current drawer now:", data.currentDrawer);
          
          // Start the timer if needed
          if (!timerRef.current) {
            startTimer();
          }
          
          setProcessingAutoStart(false);
        })
        .catch(error => {
          console.error("Auto-fixing: Error setting drawer:", error);
          setProcessingAutoStart(false);
        });
    } catch (error) {
      console.error("Auto-fixing: Unexpected error:", error);
      setProcessingAutoStart(false);
    }
  };

  // Generate hints from the word
  const generateHints = (word) => {
    // Replace all letters with underscores
    return word.split('').map(char => char === ' ' ? ' ' : '_');
  };

  // Start countdown timer
  const startTimer = () => {
    console.log("Starting timer");
    clearInterval(timerRef.current);
    
    timerRef.current = setInterval(() => {
      const roomRef = ref(database, `rooms/${roomId}`);
      get(roomRef).then((snapshot) => {
        const roomData = snapshot.val();
        if (!roomData) return;
        
        let newTimer = roomData.timer - 1;
        console.log("Timer tick:", newTimer, "State:", roomData.status);
        
        // Handle timer expiration
        if (newTimer <= 0) {
          clearInterval(timerRef.current);
          
          // Handle end of timer based on game state
          if (roomData.status === 'choosing') {
            console.log("Time's up for choosing, auto-selecting first word");
            // Auto-select first word if drawer didn't choose
            const word = roomData.wordOptions[0];
            update(roomRef, {
              status: 'drawing',
              currentWord: word,
              timer: 90,
              hints: generateHints(word),
              guessedPlayers: {} // Reset guessed players for the new round
            }).then(() => {
              console.log("Auto-selected word:", word);
              startTimer();
            });
          } else if (roomData.status === 'drawing') {
            console.log("Time's up for drawing, ending round");
            // Store the word before ending round
            endRoundWordRef.current = roomData.currentWord;
            // End the round
            endRound();
          }
        } else {
          // Update timer
          update(roomRef, { timer: newTimer });
          
          // Reveal a hint every 20 seconds
          if (roomData.status === 'drawing' && newTimer % 20 === 0) {
            revealHint(roomData.currentWord, roomData.hints);
          }
        }
      }).catch(error => {
        console.error("Error updating timer:", error);
      });
    }, 1000);
  };

  // Start the game (host only)
  const startGame = () => {
    try {
      console.log("Start game called, isHost:", isHost, "gameState:", gameState, "playerCount:", Object.keys(players).length);
      
      if (!isHost) {
        console.log("Cannot start game: User is not host");
        return;
      }
      
      if (gameState !== 'waiting') {
        console.log("Cannot start game: Game is not in waiting state");
        return;
      }
      
      if (Object.keys(players).length < 2) {
        console.log("Cannot start game: Not enough players", Object.keys(players));
        return;
      }
      
      console.log("All checks passed, starting game...");
      
      // Reset rounds counter - start at 1
      roundsRef.current = 1;
      
      // Get player IDs in a sorted, consistent order
      const playerIds = Object.keys(players).sort();
      console.log("Players available:", playerIds);
      
      // Select first drawer based on round number (1-based)
      // For round 1, drawer index should be 0
      const firstDrawerIndex = (roundsRef.current - 1) % playerIds.length;
      const firstDrawer = playerIds[firstDrawerIndex];
      console.log("Selected first drawer (index 0):", firstDrawer, "Name:", players[firstDrawer]?.name);
      
      // Generate word options
      const wordOptions = [
        getRandomWord(),
        getRandomWord(),
        getRandomWord()
      ];
      
      console.log("Word options:", wordOptions);
      
      // Use set method directly for more reliability
      const roomRef = ref(database, `rooms/${roomId}`);
      const completeRoomData = {
        ...room,  // Copy all existing room data
        status: 'choosing',
        currentDrawer: firstDrawer,
        wordOptions: wordOptions,
        timer: 30,
        roundNumber: roundsRef.current,
        guessedPlayers: {} // Reset guessed players
      };
      
      // Clear any previous drawing history
      clearDrawingHistory();
      
      set(roomRef, completeRoomData)
      .then(() => {
        console.log("Game start completed");
        return get(roomRef);
      })
      .then((snapshot) => {
        const updatedData = snapshot.val();
        console.log("After game start:", updatedData);
        console.log("Current drawer after start:", updatedData.currentDrawer);
        startTimer();
      })
      .catch(error => {
        console.error("Error starting game:", error);
      });
      
    } catch (error) {
      console.error("Unexpected error in startGame:", error);
    }
  };

  // Start a new round
  // Modified startRound function with consistent drawer rotation
  const startRound = () => {
    console.log("Using legacy startRound, consider using startNextRound instead");
    startNextRound();
  };

  // Choose a word (drawer only)
  const chooseWord = (word) => {
    console.log("Choose word called:", word);
    console.log("Current user:", currentUser.uid, "Current drawer:", currentDrawer);
    
    if (currentUser.uid !== currentDrawer) {
      console.log("Not the drawer, cannot choose word");
      return;
    }
    
    console.log("Choosing word:", word);
    
    // Update room state
    const roomRef = ref(database, `rooms/${roomId}`);
    update(roomRef, {
      status: 'drawing',
      currentWord: word,
      timer: 90, // 90 seconds to draw and guess
      hints: generateHints(word),
      guessedPlayers: {} // Reset guessed players for the new round
    }).then(() => {
      console.log("Word chosen successfully");
      
      // Reset timer
      clearInterval(timerRef.current);
      startTimer();
    }).catch(error => {
      console.error("Error choosing word:", error);
    });
  };

  // In Game.js, updated handleGuess function

  const handleGuess = (playerId, playerName, guess) => {
    console.log("Processing guess from", playerName, ":", guess);
    
    // Don't process guesses from the drawer
    if (playerId === currentDrawer) {
      console.log("Drawer cannot guess");
      return false;
    }
    
    // Check if player already guessed correctly this round
    if (guessedPlayers && guessedPlayers[playerId]) {
      console.log("Player already guessed correctly this round");
      
      // Add a system message to inform them
      const messagesRef = ref(database, `rooms/${roomId}/messages`);
      push(messagesRef, {
        text: `${playerName}, you already guessed correctly!`,
        userId: 'system',
        username: 'System',
        timestamp: Date.now(),
        type: 'system'
      });
      
      return false;
    }
    
    // Check if the guess is correct
    if (guess.toLowerCase().trim() === currentWord.toLowerCase().trim()) {
      console.log("Correct guess!");
      
      // Calculate points based on time remaining
      const pointsForGuesser = calculatePointsForGuess(timer);
      const pointsForDrawer = 25; // Fixed points for drawer
      
      // Update guessed players
      const roomRef = ref(database, `rooms/${roomId}`);
      
      // Update player scores and mark as guessed
      get(roomRef).then(snapshot => {
        const roomData = snapshot.val();
        if (!roomData) return;
        
        // Update guesser score
        const guesserScore = (roomData.players[playerId]?.score || 0) + pointsForGuesser;
        update(ref(database, `rooms/${roomId}/players/${playerId}`), {
          score: guesserScore
        });
        
        // Update drawer score
        const drawerScore = (roomData.players[currentDrawer]?.score || 0) + pointsForDrawer;
        update(ref(database, `rooms/${roomId}/players/${currentDrawer}`), {
          score: drawerScore
        });
        
        // Mark player as guessed
        const newGuessedPlayers = { ...(roomData.guessedPlayers || {}) };
        newGuessedPlayers[playerId] = true;
        update(roomRef, { guessedPlayers: newGuessedPlayers });
        
        // Add system message
        const messagesRef = ref(database, `rooms/${roomId}/messages`);
        push(messagesRef, {
          text: `${playerName} guessed the word correctly! (+${pointsForGuesser} points)`,
          userId: 'system',
          username: 'System',
          timestamp: Date.now(),
          type: 'system'
        });
        
        // Get the playerIds in a consistent way
        const playerIds = Object.keys(roomData.players || {});
        
        // Get all non-drawing players
        const nonDrawingPlayerIds = playerIds.filter(id => id !== currentDrawer);
        
        // Check who has guessed
        const guessedPlayerIds = Object.keys(newGuessedPlayers);
        
        // Count how many non-drawing players have guessed correctly
        // Filter the guessed players to only include non-drawing players
        const nonDrawersWhoGuessed = guessedPlayerIds.filter(id => nonDrawingPlayerIds.includes(id));
        const guessedCount = nonDrawersWhoGuessed.length;
        const totalNonDrawers = nonDrawingPlayerIds.length;
        
        console.log(`${guessedCount} out of ${totalNonDrawers} non-drawing players have guessed correctly`);
        console.log("Non-drawing players:", nonDrawingPlayerIds);
        console.log("Guessed players:", guessedPlayerIds);
        console.log("Non-drawers who guessed:", nonDrawersWhoGuessed);
        
        // If all non-drawing players have guessed, end the round early
        if (guessedCount >= totalNonDrawers) {
          console.log("All non-drawing players have guessed correctly, ending round");
          // Store the word before ending the round
          endRoundWordRef.current = roomData.currentWord;
          
          // End the round immediately 
          endRound();
        }
      });
      
      return true;
    }
    
    return false;
  };

  // Calculate points based on timer
  const calculatePointsForGuess = (timeLeft) => {
    // More points for faster guesses
    // Minimum 50 points, maximum 100 points
    return Math.max(50, Math.min(100, timeLeft * 2));
  };

  // Reveal a hint by uncovering a random letter
  const revealHint = (word, currentHints) => {
    if (!word || !currentHints) return;
    
    const wordArray = word.split('');
    const hintArray = [...currentHints];
    
    // Find indices of still hidden letters
    const hiddenIndices = wordArray.reduce((acc, char, idx) => {
      if (char !== ' ' && hintArray[idx] === '_') {
        acc.push(idx);
      }
      return acc;
    }, []);
    
    // If there are still hidden letters, reveal one randomly
    if (hiddenIndices.length > 0) {
      const randomIndex = hiddenIndices[Math.floor(Math.random() * hiddenIndices.length)];
      hintArray[randomIndex] = wordArray[randomIndex];
      
      console.log("Revealing hint:", hintArray.join(''));
      
      // Update hints in database
      const roomRef = ref(database, `rooms/${roomId}`);
      update(roomRef, { hints: hintArray });
    }
  };

  // End the current round
  const endRound = () => {
    console.log("Ending round", roundsRef.current);
    
    // Get the word from the ref to prevent using the wrong word
    const wordToAnnounce = endRoundWordRef.current || currentWord;
    console.log("Word to announce at end of round:", wordToAnnounce);
    
    // Update room state
    const roomRef = ref(database, `rooms/${roomId}`);
    update(roomRef, {
      status: 'roundEnd',
      timer: 10 // 10 seconds before next round
    }).then(() => {
      console.log("Round ended successfully");
      
      // Add system message with the word, but only if we have a word to announce
      if (wordToAnnounce) {
        const messagesRef = ref(database, `rooms/${roomId}/messages`);
        push(messagesRef, {
          text: `The word was: ${wordToAnnounce}`,
          userId: 'system',
          username: 'System',
          timestamp: Date.now(),
          type: 'system'
        });
        
        // Clear the stored word to prevent duplicate announcements
        endRoundWordRef.current = '';
      }
      
      // Check if game should end
      const totalRounds = room.rounds || 3;
      const totalPlayers = Object.keys(players).length;
      const maxRounds = totalRounds * totalPlayers;
      
      console.log(`Current round: ${roundsRef.current}, Max rounds: ${maxRounds}`);
      
      if (roundsRef.current >= maxRounds) {
        console.log("Game complete, ending game after 10 seconds");
        setTimeout(() => {
          update(roomRef, { status: 'gameEnd' })
            .then(() => {
              console.log("Game ended successfully");
              
              // Get fresh player data from the database
              get(ref(database, `rooms/${roomId}/players`)).then(snapshot => {
                const currentPlayers = snapshot.val() || {};
                
                // Calculate scores with the latest data
                const playerScores = Object.values(currentPlayers).map(p => ({
                  id: p.id,
                  name: p.name,
                  score: p.score || 0
                }));
                
                playerScores.sort((a, b) => b.score - a.score);
                const highestScore = playerScores[0].score;
                const winners = playerScores.filter(p => p.score === highestScore);
                
                // Announce with correct scores
                const messagesRef = ref(database, `rooms/${roomId}/messages`);
                if (winners.length === 1) {
                  push(messagesRef, {
                    text: `Game over! ${winners[0].name} wins with ${winners[0].score} points!`,
                    userId: 'system',
                    username: 'System',
                    timestamp: Date.now(),
                    type: 'system'
                  });
                } else {
                  const winnerNames = winners.map(w => w.name).join(', ');
                  push(messagesRef, {
                    text: `Game over! It's a tie between ${winnerNames} with ${highestScore} points!`,
                    userId: 'system',
                    username: 'System',
                    timestamp: Date.now(),
                    type: 'system'
                  });
                }
              });
            });
        }, 10000);
      } else {
        console.log("Starting next round in 10 seconds");
        // Start next round after 10 seconds
        setTimeout(() => {
          startNextRound();
        }, 10000);
      }
    }).catch(error => {
      console.error("Error ending round:", error);
    });
  };

  const startNextRound = () => {
    try {
      console.log("Starting next round from current round", roundsRef.current);
      
      // Increment round counter explicitly
      const nextRound = roundsRef.current + 1;
      console.log("Next round will be", nextRound);
      
      // Get player IDs in a deterministic order
      const playerIds = Object.keys(players).sort();
      
      if (playerIds.length < 2) {
        console.error("Not enough players to start round");
        return;
      }
      
      // Explicitly calculate the next drawer based on the next round number
      const nextDrawerIndex = (nextRound - 1) % playerIds.length;
      const nextDrawer = playerIds[nextDrawerIndex];
      
      console.log(`Next round ${nextRound}, selecting drawer index ${nextDrawerIndex} (${nextDrawer})`);
      console.log("Next drawer name:", players[nextDrawer]?.name);
      
      // Generate word options
      const wordOptions = [
        getRandomWord(),
        getRandomWord(),
        getRandomWord()
      ];
      
      // Clear the canvas
      clearDrawingHistory();
      
      // Update Firebase with complete data
      const roomRef = ref(database, `rooms/${roomId}`);
      get(roomRef).then((snapshot) => {
        const currentRoomData = snapshot.val();
        if (!currentRoomData) return;
        
        // Create a complete update with all the critical fields
        const updatedRoom = {
          ...currentRoomData,
          status: 'choosing',
          currentDrawer: nextDrawer,
          wordOptions: wordOptions,
          timer: 30,
          roundNumber: nextRound, // Critical: update the round number in Firebase
          hints: [],
          guessedPlayers: {}
        };
        
        // Update local round reference
        roundsRef.current = nextRound;
        
        return set(roomRef, updatedRoom);
      })
      .then(() => {
        console.log("New round started successfully");
        console.log("Current drawer set to:", nextDrawer);
        console.log("Round counter updated to:", roundsRef.current);
        
        return get(roomRef);
      })
      .then((snapshot) => {
        const data = snapshot.val();
        console.log("Verification - Updated room data:", data);
        console.log("Verification - Current drawer in Firebase:", data.currentDrawer);
        console.log("Verification - Round number in Firebase:", data.roundNumber);
        
        // Start timer
        startTimer();
      })
      .catch(error => {
        console.error("Error starting next round:", error);
      });
    } catch (error) {
      console.error("Error in startNextRound:", error);
    }
  };

  // Force start the game (for debugging)
  const forceStartGame = () => {
    try {
      console.log("Force starting game...");
      
      // Get player IDs
      const playerIds = Object.keys(players);
      if (playerIds.length < 2) {
        console.log("Not enough players to force start");
        return;
      }
      
      // Reset round counter - explicitly set to 1
      roundsRef.current = 1;
      
      // Use proper index calculation: (roundNumber - 1) % playerCount
      const firstDrawerIndex = (roundsRef.current - 1) % playerIds.length;
      const firstDrawer = playerIds[firstDrawerIndex];
      console.log("Setting drawer to:", firstDrawer, "Name:", players[firstDrawer]?.name);
      
      // Generate word options
      const wordOptions = [
        getRandomWord(),
        getRandomWord(),
        getRandomWord()
      ];
      
      // Create a complete game state update
      const gameStateUpdate = {
        status: 'choosing',
        currentDrawer: firstDrawer,
        wordOptions: wordOptions,
        timer: 30,
        roundNumber: roundsRef.current,  // Always include round number
        hints: [],
        guessedPlayers: {} // Reset guessed players
      };
      
      console.log("Forcing game state:", gameStateUpdate);
      
      // Clear drawing history
      clearDrawingHistory();
      
      // Update directly with set to ensure all fields are updated
      const roomRef = ref(database, `rooms/${roomId}`);
      
      // First get current room data
      get(roomRef)
        .then((snapshot) => {
          const currentRoomData = snapshot.val();
          
          // Create a complete room object with our game state changes
          const updatedRoom = {
            ...currentRoomData,
            ...gameStateUpdate  // Override with our game state
          };
          
          // Use set to completely replace the room data
          return set(roomRef, updatedRoom);
        })
        .then(() => {
          console.log("Force start successful");
          return get(roomRef);
        })
        .then((snapshot) => {
          const result = snapshot.val();
          console.log("Game state after force start:", result);
          console.log("Status:", result.status);
          console.log("Drawer:", result.currentDrawer);
          
          // Manually update local state to ensure UI updates
          setGameState('choosing');
          setCurrentDrawer(firstDrawer);
          
          // Start the timer
          startTimer();
        })
        .catch((error) => {
          console.error("Error force starting game:", error);
        });
    } catch (error) {
      console.error("Unexpected error in forceStartGame:", error);
    }
  };

  // Test updating drawer directly - for debugging
  const testUpdateDrawer = () => {
    const roomRef = ref(database, `rooms/${roomId}`);
    const playerIds = Object.keys(players);
    if (playerIds.length > 0) {
      const testDrawer = playerIds[0];
      console.log("Testing update drawer with:", testDrawer);
      
      // Simple direct update test
      update(roomRef, {
        currentDrawer: testDrawer
      })
      .then(() => {
        console.log("Direct drawer update successful");
        return get(roomRef);
      })
      .then(snapshot => {
        console.log("Verification after direct update:", snapshot.val());
      })
      .catch(error => {
        console.error("Direct update failed:", error);
      });
    }
  };

  // Render debug info button
  const renderDebugButton = () => {
    return (
      <div style={{ position: 'fixed', bottom: '10px', right: '10px', display: 'flex', flexDirection: 'column', gap: '5px' }}>
        <button 
          onClick={forceStartGame}
          style={{ 
            background: '#4caf50',
            color: 'white',
            border: '1px solid #ccc',
            padding: '5px 10px',
            borderRadius: '4px',
            fontSize: '12px',
            fontWeight: 'bold'
          }}
        >
          Force Start Game
        </button>
        <button 
          onClick={testUpdateDrawer}
          style={{ 
            background: '#ff9800',
            border: '1px solid #ccc',
            padding: '5px 10px',
            borderRadius: '4px',
            fontSize: '12px'
          }}
        >
          Test Direct Drawer Update
        </button>
        <button 
          onClick={() => console.log({
            room, 
            players, 
            gameState, 
            currentDrawer, 
            currentUser: currentUser?.uid,
            isDrawer: currentDrawer === currentUser?.uid,
            currentWord,
            guessedPlayers,
            round: roundsRef.current
          })}
          style={{ 
            background: '#f0f0f0',
            border: '1px solid #ccc',
            padding: '5px 10px',
            borderRadius: '4px',
            fontSize: '12px'
          }}
        >
          Debug Info
        </button>
      </div>
    );
  };

  // Render lobby if game state is 'waiting'
  if (gameState === 'waiting') {
    return (
      <>
        <Lobby roomId={roomId} isHost={isHost} />
        {renderDebugButton()}
        </>
    );
  }

  // Render main game for other game states
  return (
    <div className="game-container">
      <div className="game-header">
  <h2>Room: {room?.name || 'Loading...'}</h2>
  <div style={{ textAlign: 'center' }}>
    <p style={{ margin: '0', fontSize: '14px' }}>
      <strong>Game State:</strong> {gameState} | 
      <strong> Current Drawer:</strong> {currentDrawer === currentUser?.uid ? 'You' : players[currentDrawer]?.name || 'None'} |
      <strong> Round:</strong> {roundsRef.current}
    </p>
  </div>
  <Timer seconds={timer} gameState={gameState} />
</div>
      
      <div className="game-content">
        <div className="left-panel">
          <PlayerList 
            players={players} 
            currentDrawer={currentDrawer}
            guessedPlayers={guessedPlayers}
          />
          
          <ChatBox 
            roomId={roomId} 
            currentUser={currentUser}
            currentWord={currentWord}
            gameState={gameState}
            currentDrawer={currentDrawer}
            handleGuess={handleGuess}
          />
        </div>
        
        <div className="center-panel">
          <div className="canvas-container">
            {gameState === 'choosing' && currentDrawer === currentUser?.uid && (
              <div className="word-selection">
                <h3>Choose a word to draw:</h3>
                <div className="word-options">
                  {room?.wordOptions?.map((word, index) => (
                    <button key={index} onClick={() => chooseWord(word)}>
                      {word}
                    </button>
                  ))}
                </div>
              </div>
            )}
            
            {gameState === 'choosing' && currentDrawer !== currentUser?.uid && (
              <div className="waiting-message">
                <h3>Waiting for {players[currentDrawer]?.name || 'drawer'} to choose a word...</h3>
              </div>
            )}
            
            {gameState === 'drawing' && (
              <div className="word-display">
                {currentDrawer === currentUser?.uid ? (
                  <h3>Draw: {currentWord}</h3>
                ) : (
                  <h3>Word: {hints.join(' ')}</h3>
                )}
              </div>
            )}
            
            <Canvas 
              roomId={roomId}
              isDrawer={currentDrawer === currentUser?.uid}
              gameState={gameState}
              brushColor={selectedColor}
              brushSize={brushSize}
            />
            
            {gameState === 'drawing' && currentDrawer === currentUser?.uid && (
              <div className="drawing-tools">
                <ColorPicker 
                  selectedColor={selectedColor} 
                  setSelectedColor={setSelectedColor} 
                />
                <div className="brush-size">
                  <label>Brush Size</label>
                  <input 
                    type="range" 
                    min="1" 
                    max="20" 
                    value={brushSize} 
                    onChange={(e) => setBrushSize(Number(e.target.value))} 
                  />
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      
      <GameControls 
        isHost={isHost}
        gameState={gameState}
        playerCount={Object.keys(players).length}
        startGame={startGame}
        roomId={roomId}
      />
      
      {renderDebugButton()}
    </div>
  );
};

export default Game;