import React, { createContext, useContext, useState, useEffect } from 'react';
import { auth } from '../firebase/config';
import { signInAnonymously, updateProfile, signOut } from 'firebase/auth';

const AuthContext = createContext();

export function useAuth() {
  return useContext(AuthContext);
}

export function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    console.log("Setting up auth state change listener");
    
    const unsubscribe = auth.onAuthStateChanged(user => {
      console.log("Auth state changed:", user);
      
      if (user) {
        // Make sure we have all the necessary user properties
        const enhancedUser = {
          ...user,
          uid: user.uid || `anonymous-${Date.now()}`, // Ensure there's always a uid
          displayName: user.displayName || 'Anonymous'
        };
        
        console.log("Setting enhanced user:", enhancedUser);
        setCurrentUser(enhancedUser);
      } else {
        console.log("Setting user to null");
        setCurrentUser(null);
      }
      
      setLoading(false);
    });

    return unsubscribe;
  }, []);

  // Function to sign in anonymously and set a display name
  const login = async (displayName) => {
    try {
      // Make sure we have a valid display name
      if (!displayName || displayName.trim() === '') {
        throw new Error('Display name is required');
      }
      
      // First sign in anonymously
      const result = await signInAnonymously(auth);
      console.log("Anonymous sign-in successful");
      
      // Then update the profile with the display name
      await updateProfile(result.user, { 
        displayName: displayName.trim() 
      });
      console.log("Profile updated with display name:", displayName.trim());
      
      // Get the updated user
      const user = auth.currentUser;
      console.log("Current user after update:", user);
      
      // Force a state update with the updated user info
      setCurrentUser({
        ...user,
        displayName: displayName.trim() // Explicitly include the display name
      });
      
      return user;
    } catch (error) {
      console.error("Error signing in:", error);
      throw error;
    }
  };

  // Function to sign out
  const logout = async () => {
    try {
      await signOut(auth);
      return true;
    } catch (error) {
      console.error("Error signing out:", error);
      throw error;
    }
  };

  const value = {
    currentUser,
    login,
    logout
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
}