import React from 'react';
import { useNavigate } from 'react-router-dom';
import { database } from '../firebase/config';
import { ref, get, update, set } from 'firebase/database';
import { getRandomWord } from '../utils/wordGenerator';
import '../styles/GameControls.css';

const GameControls = ({ isHost, gameState, playerCount, startGame, roomId }) => {
  const navigate = useNavigate();

  const handleStartGame = () => {
    console.log("GameControls: Start game button clicked");
    console.log("GameControls: isHost:", isHost);
    console.log("GameControls: gameState:", gameState);
    console.log("GameControls: playerCount:", playerCount);
    console.log("GameControls: roomId:", roomId);
    
    // Check conditions before proceeding
    if (!isHost || gameState !== 'waiting' || playerCount < 2) {
      console.log("GameControls: Cannot start game: conditions not met");
      return;
    }
    
    // Always use the startGame function from Game.js if available
    if (typeof startGame === 'function') {
      console.log("GameControls: Using parent component startGame function");
      startGame();
      return;
    }
    
    // This is a fallback and should ideally not be used
    console.log("GameControls: FALLBACK - Starting game directly - This should not happen normally");
    const roomRef = ref(database, `rooms/${roomId}`);
    
    // Get current players to select first drawer
    get(roomRef).then((snapshot) => {
      const roomData = snapshot.val();
      if (!roomData) {
        console.error("GameControls: No room data found");
        return;
      }
      
      const playerIds = Object.keys(roomData.players || {});
      if (playerIds.length < 2) {
        console.log("GameControls: Not enough players to start game");
        return;
      }
      
      console.log("GameControls: Players:", playerIds);
      
      // Always use first player as drawer for first round (round 1)
      // Use (roundNumber - 1) for correct indexing
      const roundNumber = 1;
      const firstDrawerIndex = (roundNumber - 1) % playerIds.length; // Should be 0 for first round
      const firstDrawer = playerIds[firstDrawerIndex];
      
      console.log("GameControls: Selected first drawer:", firstDrawer);
      console.log("GameControls: Drawer name:", roomData.players[firstDrawer]?.name);
      
      // Generate word options
      const wordOptions = [
        getRandomWord(),
        getRandomWord(),
        getRandomWord()
      ];
      
      console.log("GameControls: Word options:", wordOptions);
      
      // Try both approaches to maximize chance of success
      tryUpdateMethod(roomRef, roomData, firstDrawer, wordOptions);
    }).catch(error => {
      console.error("GameControls: Error fetching room data:", error);
    });
  };

  // Try update method first
  const tryUpdateMethod = (roomRef, roomData, firstDrawer, wordOptions) => {
    console.log("GameControls: Trying update method");
    
    // Create update object
    const updates = {
      status: 'choosing',
      currentDrawer: firstDrawer,
      wordOptions: wordOptions,
      timer: 30,
      roundNumber: 1 // Always start at round 1
    };
    
    console.log("GameControls: Update payload:", updates);
    
    // Update room state
    update(roomRef, updates)
      .then(() => {
        console.log("GameControls: Update method completed");
        return get(roomRef);
      })
      .then((snapshot) => {
        const updatedData = snapshot.val();
        console.log("GameControls: After update method:", updatedData);
        console.log("GameControls: Current drawer after update:", updatedData.currentDrawer);
        
        // If drawer wasn't set, try set method
        if (!updatedData.currentDrawer) {
          console.log("GameControls: Update didn't set drawer, trying set method");
          trySetMethod(roomRef, roomData, firstDrawer, wordOptions);
        }
      })
      .catch(error => {
        console.error("GameControls: Error in update method:", error);
        // Try set method as fallback
        trySetMethod(roomRef, roomData, firstDrawer, wordOptions);
      });
  };
  
  // Try set method as a fallback
  const trySetMethod = (roomRef, roomData, firstDrawer, wordOptions) => {
    console.log("GameControls: Trying set method");
    
    // Create complete room data
    const completeRoomData = {
      ...roomData,
      status: 'choosing',
      currentDrawer: firstDrawer,
      wordOptions: wordOptions,
      timer: 30,
      roundNumber: 1 // Always start at round 1
    };
    
    console.log("GameControls: Set payload (first few fields):", 
      JSON.stringify({
        status: completeRoomData.status,
        currentDrawer: completeRoomData.currentDrawer,
        host: completeRoomData.host,
        roundNumber: completeRoomData.roundNumber
      }));
    
    // Set complete room data
    set(roomRef, completeRoomData)
      .then(() => {
        console.log("GameControls: Set method completed");
        return get(roomRef);
      })
      .then((snapshot) => {
        const setData = snapshot.val();
        console.log("GameControls: After set method:", setData);
        console.log("GameControls: Current drawer after set:", setData.currentDrawer);
        console.log("GameControls: Round number after set:", setData.roundNumber);
      })
      .catch(error => {
        console.error("GameControls: Error in set method:", error);
        // Try direct drawer update as last resort
        console.log("GameControls: Attempting last resort: direct drawer update");
        update(roomRef, {
          currentDrawer: firstDrawer,
          roundNumber: 1
        })
          .then(() => console.log("GameControls: Direct drawer update done"))
          .catch(err => console.error("GameControls: Direct drawer update failed:", err));
      });
  };

  const handleLeaveGame = () => {
    console.log("GameControls: Leave game button clicked");
    navigate('/');
  };

  return (
    <div className="game-controls">
      {isHost && gameState === 'waiting' && (
        <button 
          className={`start-game-btn ${playerCount < 2 ? 'disabled' : ''}`}
          onClick={handleStartGame}
          disabled={playerCount < 2}
        >
          {playerCount < 2 ? 'Need at least 2 players' : 'Start Game'}
        </button>
      )}
      
      <button className="leave-game-btn" onClick={handleLeaveGame}>
        Leave Game
      </button>
    </div>
  );
};

export default GameControls;